// ==================================================
// Carbon Ads
// ==================================================
#carbonads {
  padding-bottom: em(40);

  a {
    text-decoration: none;
  }

  > span {
    width: 100%;
    max-width: em(300);
    margin-left: auto;
    margin-right: auto;
    display: block;
    position: relative;
  }
}

.carbon-wrap {
  width: 100%;
  display: table;
  position: relative;
}

.carbon-img,
.carbon-text {
  display: table-cell;
  vertical-align: middle;
  text-align: left;
}

.carbon-img {
  background-color: $white;

  img {
    display: block;
  }
}

.carbon-text {
  font-size: em(12);
  line-height: 1.2;
  padding-left: em(15, 12);
}

.carbon-poweredby {
  font-size: em(11);
  color: $grey;
  text-align: center;
  width: 130px;
  position: absolute;
  bottom: -20px;
  left: 0;
}

@media only screen and (min-width: em(1100)) {
  #carbonads {
    padding-bottom: 0;
    position: absolute;
    top: em(340);
    right: em(30);

    > span {
      max-width: em(130);
      position: static;
    }
  }

  .carbon-img,
  .carbon-text {
    display: block;
  }

  .carbon-text {
    padding-top: em(7, 12);
    padding-left: 0;
  }
}
